@font-face {
    font-family: Sansation-Light; /* Имя шрифта */
    src: url('../fonts/Sansation-Light.ttf'); /* Путь к файлу со шрифтом */
}

@font-face {
    font-family: Solway-Medium; /* Имя шрифта */
    src: url('../fonts/Solway-Medium.ttf'); /* Путь к файлу со шрифтом */
}


.HomePageContainer{
    display: flex;
    flex-direction: row;
    width:100%;
    height:88vh;
}


.HomePageTitleContainer{
    display: flex;
    flex-direction: column;
    margin-left: 10%;
    width:55%;
    line-height: 0;
    margin-top: 21%;
}

.HomePageTitleOne{
    font-family: Solway-Medium;
    font-weight: lighter;
    font-size: 72px;
    color: #770505;
    line-height: 0;
    padding:0;
    margin:1%;
}

.HomePageTitleTwo{
    font-family: Sansation-Light;
    font-weight: 100;
    font-size: 72px;
    color: #770505;
    line-height: 0;
    margin-left: 18%;
    line-height: 0;

}


.HomePagePictureContainer{
    display: flex;
    align-self: center;
    justify-self: center;
    width:10%;
}

.HomePagePicture{
    width:35rem;
    height:35rem;
}