.NavMenu{
 display:flex;
 flex-direction: row;
 width: 100%;
 height:10vh;
}

.LogoImg{
 width:17%;
 height:10vh;
 
}

.NavButtons{
 display:flex;
 flex-direction: row;
 justify-content: center;
 align-items: center;
 list-style-type: none;
  margin-left: 4%;
  width:60%;
}

.Buttons{
 color:white;
 margin-left: 5%;
 font-size: 1.2rem;
}

.Buttons:hover{
 color:#770505;
 box-shadow: #770505  1px 2px 20px;
 cursor:pointer;
 transition-duration: .4s;
 transition-timing-function: ease-in-out;
}

.langButtons{
 display: flex;
 color: white;
 border: none;
 background-color: black;
 height:1rem;
 width:4rem;
 align-self: center;
 align-items:baseline ;
 justify-content:center;
}

.langButtons:hover{
 color:#770505;
 cursor:pointer;
 transition-duration: .2s;
 box-shadow: #770505  1px 2px 20px;
}